function Homepage(props) {
  return <div className="content">
    <h1>{props.name}</h1>
    </div>;
}

function Webpage(props) {
  return <div className="content">
  <h1>web</h1>
  </div>;
}

function Gamepage(props) {
  return <div className="content">
  <h1>game</h1>
  </div>;
}

//mainEl = <ReactMarkdown source={post2} />;
function Main() {
  return (
    <Router>
      <Nav />
      <Switch>
        <Route path="/portfolio" render={(props)=> <Homepage {...props} name="HOMEPAGE!"/>} />
        <Route path="/web" render={(props)=> <ShowPosts {...props} category="web"/>} />
        <Route path="/web/:id" component={Webpage} />
        <Route path="/game" component={(props)=> <ShowPosts {...props} category="game"/>} />
        <Route path="/game/:id" component={Gamepage} />
      </Switch>
    </Router>
  );
}


/*
ReactDOM.render(
  <Nav />,
  document.getElementById('nav')
);
*/

ReactDOM.render(
  <Main />,
  document.getElementById('main')
);


